.questions {
  @include grid;

  position: relative;

  padding-top: 155px;

  @include for-tablet {
    padding-top: 120px;
  }

  @include for-desktop {
    padding-top: 190px;
  }

  &__image {
    &-wrapper {
      position: absolute;

      top: 37px;
      right: 0;

      @include for-tablet {
        top: -4px;
      }

      @include for-desktop {
        top: 57px;
        right: calc(
          grid-margin-size(
            $amount-of-desktop-grid-boxes,
            $desktop-grid-box-size,
            $desktop-grid-gap-size
          ) - 74px
        );
      }
    }

    display: block;
  }

  &__trapezoid {
    @include trapezoid(560px, 406px, 12, 0, 0);

    top: 152px;

    @include for-tablet {
      @include trapezoid(477px, 385px, 6, 0, 1);

      top: 103px;
    }

    @include for-mobile {
      @include trapezoid(1003px, 916px, 2, 0, 1);

      top: 110px;
    }
  }

  &__title {
    color: black;

    grid-row: 1 / 2;
    grid-column: 1 / 5;

    @include for-mobile {
      grid-column: 1 / 3;
    }
  }

  &__description {
    color: black;

    margin-bottom: 30px;

    grid-row: 2 / 3;
    grid-column: 1 / 3;

    @include for-mobile {
      margin-bottom: 40px;
    }

    @include for-tablet {
      grid-column: 1 / 4;
    }

    @include for-desktop {
      grid-column: 1 / 5;
    }
  }

  &__message {
    grid-row: 3 / 4;
    grid-column: 1 / 3;

    @include for-mobile {
      margin-bottom: 56px;
    }

    @include for-tablet {
      grid-column: 1 / 4;
    }

    @include for-desktop {
      grid-column: 1 / 5;
    }
  }

  &__address {
    grid-row: 1 / 4;
    grid-column: 7 / 10;

    @include for-desktop {
      padding-top: 42px;
    }

    @include for-tablet {
      padding-top: 54px;

      grid-column: 5 / 7;
    }

    @include for-mobile {
      grid-row: 4 / 5;
      grid-column: 1 / 3;
    }
  }
}
