.potr-team {
  @include grid;

  position: relative;
  overflow: hidden;

  max-width: 100%;
  padding-top: 110px;

  @include for-tablet {
    padding-top: 132px;
  }

  @include for-desktop {
    padding-top: 150px;
  }

  &__trapezoid {
    @include trapezoid(311px, 164px, 3, 1, 0);

    top: 335px;

    @include for-tablet {
      @include trapezoid(233px, 164px, 2, 0, 0);

      top: 302px;
    }

    @include for-mobile {
      @include trapezoid(293px, 198px, 2, 0, 1);

      top: auto;
      bottom: 0;
    }
  }

  &__title {
    margin-bottom: 25px;

    color: $big-grey-text-color;

    grid-column: 1 / 3;

    @include for-tablet {
      margin-bottom: 177px;

      grid-column: 1 / 6;
    }

    @include for-desktop {
      margin-bottom: 143px;

      grid-column: 1 / 7;
    }
  }

  &__image {
    &-wrapper {
      justify-self: end;

      grid-column: 4 / 12;
      grid-row: 2 / 3;

      @include for-tablet {
        grid-column: 4 / 7;
        grid-row: 2 / 3;
      }

      @include for-mobile {
        justify-self: center;
        margin-bottom: 30px;

        grid-column: 1 / 3;
      }
    }

    display: block;

    @include for-mobile {
      width: var(--cw);
    }
  }

  &__description {
    color: $medium-grey-text-color;

    @include for-mobile {
      margin-bottom: 58px;

      grid-column: 1 / 3;
    }

    @include for-tablet {
      padding-top: 53px;
      margin-bottom: 113px;

      grid-column: 1 / 4;
      grid-row: 2 / 3;
    }

    @include for-desktop {
      width: 90%;

      padding-top: 230px;
      margin-bottom: 240px;

      grid-column: 1 / 5;
      grid-row: 2 / 3;
    }
  }

  &__name {
    position: absolute;

    color: transparent;
    -webkit-text-stroke: 3px $main-accent;

    @include for-mobile {
      display: none;
    }

    @include for-tablet {
      left: -47px;
      top: 219px;

      &:last-child {
        top: 597px;
        left: auto;
        right: -32px;
      }
    }

    @include for-desktop {
      left: 73px;
      top: 247px;

      &:last-child {
        left: auto;
        right: -45px;
        top: 817px;
      }
    }
  }
}
